.cart-container {
    margin-top: 60px;
    margin-bottom: 60px;
    h1 {
        text-align: center;
    }
    .cart-food {
        margin-top: 60px;
        margin-left: 20%;
        margin-right: 20%;
        display: flex;
        justify-content: space-evenly;
        .cart-food__name {
            font-size: 0.9rem;
            font-weight: 900;
        }
        .cart-food-money {
            font-size: 0.9rem;
            font-weight: 900;
        }
        .cart-food__button {
            transform: translateY(-10px);
            Button {
                margin-left: 20px;
                font-weight: 800;
                font-size: 1rem;
            }
        }
    }
    .cart-total {
        margin-top: 60px;
        margin-left: 10%;
        margin-right: 10%;
        display: flex;
        justify-content: space-evenly;
        .cart-total__quantity {
            font-size: 0.9rem;
            font-weight: 900;
            button {
                margin-left: 10px;
            }
        }
        .cart-total__money {
            font-size: 0.9rem;
            font-weight: 900;
            margin-left: 20px;
            button {
                margin-left: 10px;
            }
        }
        .cart-total__timeremaning {
            // transform: translateY(-7px);
            font-size: 0.9rem;
            font-weight: 900;
            margin-left: 20px;
            button {
                margin-left: 10px;
            }
        }
        .cart-total__button {
            // transform: translateY(-10px);
            button {
                font-weight: 800;
                font-size: 0.9rem;
                background: #ffcb00;
            }
        }
    }
}
